import React, { useState } from "react";
import "./App.css";
import { useNuiEvent } from "../hooks/useNuiEvent";

interface CheckProps {
  onButtonClick: (buttonType: string) => void;
}

const Remove: React.FC<CheckProps> = ({ onButtonClick }) => {
  const [officersCount, setOfficersCount] = useState(0);
  useNuiEvent<any>("getOfficers", (data) => {
    setOfficersCount(data.count);
  });

  return (
    <div id="police-hub-container" className="police-hub-container">
      <div className="hub-container">
        <div className="police-hub">
          <h1>POLICE HUB</h1>
          <div className="purple-line"></div>
        </div>
        <div className="hub-buttons">
          <div onClick={() => onButtonClick("index")} className="hub-button">
            <h1 id="active-officers">{officersCount}</h1>
          </div>
          <div
            onClick={() => onButtonClick("mdtt")}
            id="tablet"
            className="hub-button"
          >
            <i className="fa-solid fa-tablet-button"></i>
          </div>
          <div
            onClick={() => onButtonClick("callsign")}
            id="pen"
            className="hub-button"
          >
            <i className="fa-solid fa-file-pen"></i>
          </div>
          <div id="bars" className="hub-button toggle">
            <i className="fa-solid fa-bars"></i>
          </div>
        </div>
      </div>
      <div>
        <div id="flag-duty-options">
          <button
            onClick={() => onButtonClick("check")}
            style={{ marginBottom: "5px" }}
          >
            Flagged Menu
          </button>
          <button onClick={() => onButtonClick("duty")}>Duty Status</button>
        </div>
      </div>
    </div>
  );
};

export default Remove;
