import React, { useEffect, useRef, useState } from "react";
import "./App.css";
import { useNuiEvent } from "../hooks/useNuiEvent";
import Draggable from "react-draggable";
import Check from "./Check";
import NewFlag from "./newflag";
import Remove from "./remove";
import Settings from "./settings";
import Duty from "./duty";
import { debugData } from "../utils/debugData";
import { fetchNui } from "../utils/fetchNui";
import Callsign from "./callsign";

debugData([
  {
    action: "setVisible",
    data: true,
  },
]);

interface officerData {
  status: boolean;
  callsign: string;
  name: string;
  frequency: string;
}

const App: React.FC = () => {
  const Draggable1: any = Draggable;
  const [activeComponent, setActiveComponent] = useState("default");

  const [draggable, setDraggable] = useState(false);
  useNuiEvent<boolean>("setDraggable", setDraggable);

  const [officersData, setOfficersData] = useState<officerData[]>([
    {
      status: true,
      callsign: "C-0",
      name: "moat al3m",
      frequency: "1",
    },
    {
      status: true,
      callsign: "C-0",
      name: "moat al3m",
      frequency: "1",
    },
    {
      status: true,
      callsign: "C-0",
      name: "moat al3m",
      frequency: "1",
    },
    {
      status: true,
      callsign: "C-0",
      name: "moat al3m",
      frequency: "1",
    },
    {
      status: true,
      callsign: "C-0",
      name: "moat al3m",
      frequency: "1",
    },
    {
      status: true,
      callsign: "C-0",
      name: "moat al3m",
      frequency: "1",
    },
  ]);
  useNuiEvent<any>("officersData", (data) => {
    setOfficersData(data.officers);
  });

  const [officersCount, setOfficersCount] = useState(0);
  useNuiEvent<any>("getOfficers", (data) => {
    setOfficersCount(data.count);
  });

  const handleBarsClick = (component: string) => {
    if (component == "mdtt") {
      fetchNui("showMDT"); // I love react LOL
    } else setActiveComponent(component);
  };

  const renderComponent = () => {
    switch (activeComponent) {
      case "check":
        return <Check onButtonClick={handleBarsClick} />;
      case "newflag":
        return <NewFlag onButtonClick={handleBarsClick} />;
      case "removeflag":
        return <Remove onButtonClick={handleBarsClick} />;
      case "settings":
        return <Settings onButtonClick={handleBarsClick} />;
      case "duty":
        return <Duty onButtonClick={handleBarsClick} />;
      case "callsign":
        return <Callsign onButtonClick={handleBarsClick} />;
      default:
        return (
          <div id="police-hub-container" className="police-hub-container">
            <div className="hub-container">
              <div className="police-hub">
                <h1>POLICE HUB</h1>
                <div className="purple-line"></div>
              </div>
              <div className="hub-buttons">
                <div className="hub-button">
                  <h1 id="active-officers">{officersCount}</h1>
                </div>
                <div
                  onClick={() => handleBarsClick("mdtt")}
                  id="tablet"
                  className="hub-button"
                >
                  <i className="fa-solid fa-tablet-button"></i>
                </div>
                <div
                  onClick={() => handleBarsClick("callsign")}
                  id="pen"
                  className="hub-button"
                >
                  <i className="fa-solid fa-file-pen"></i>
                </div>
                <div
                  onClick={() => handleBarsClick("settings")}
                  id="bars"
                  className="hub-button"
                >
                  <i className="fa-solid fa-bars"></i>
                </div>
              </div>
            </div>
            <div id="officers-container" className="officers-container">
              {officersData.map((officer, index) => (
                <div className="officer" key={index}>
                  <div className={officer.status ? "green" : "out"}></div>
                  <h1 className="officer-callsign">{officer.callsign}</h1>
                  <h1 className="officer-name">{officer.name}</h1>
                  <div className="officer-radio-freq">
                    <h1>{officer.frequency ? officer.frequency : "OFF"}</h1>
                  </div>
                </div>
              ))}
            </div>
          </div>
        );
    }
  };

  useEffect(() => {
    if (!draggable) return;

    const keyHandler = (e: KeyboardEvent) => {
      if (["KeyP"].includes(e.code)) {
        fetchNui("SetFocus", { Focus: false });
      }
    };

    window.addEventListener("keydown", keyHandler);

    return () => window.removeEventListener("keydown", keyHandler);
  }, [draggable]);

  return draggable ? (
    <Draggable1>
      <div className="app" id="app">
        {renderComponent()}
      </div>
    </Draggable1>
  ) : (
    <div className="app" id="app">
      {renderComponent()}
    </div>
  );
};

export default App;
