import React, { useState } from "react";
import "./App.css";
import { fetchNui } from "../utils/fetchNui";
import { useNuiEvent } from "../hooks/useNuiEvent";

interface CheckProps {
  onButtonClick: (buttonType: string) => void;
}

const Duty: React.FC<CheckProps> = ({ onButtonClick }) => {
  const [officersCount, setOfficersCount] = useState(0);

  useNuiEvent<any>("getOfficers", (data) => {
    setOfficersCount(data.count);
  });

  const [status, setStatus] = useState("");
  useNuiEvent<any>("getDuty", (data) => {
    setStatus(data.status);
  });

  const changeDuty = (duty: boolean) => {
    fetchNui("changeOfficerDuty", { duty }); // I love react LOL
  };

  return (
    <div id="police-hub-container" className="police-hub-container">
      <div className="hub-container">
        <div className="police-hub">
          <h1>POLICE HUB</h1>
          <div className="purple-line"></div>
        </div>
        <div className="hub-buttons">
          <div onClick={() => onButtonClick("index")} className="hub-button">
            <h1 id="active-officers">{officersCount}</h1>
          </div>
          <div
            onClick={() => onButtonClick("mdtt")}
            id="tablet"
            className="hub-button"
          >
            <i className="fa-solid fa-tablet-button"></i>
          </div>
          <div
            onClick={() => onButtonClick("callsign")}
            id="pen"
            className="hub-button"
          >
            <i className="fa-solid fa-file-pen"></i>
          </div>
          <div
            onClick={() => onButtonClick("settings")}
            id="bars"
            className="hub-button toggle"
          >
            <i className="fa-solid fa-bars"></i>
          </div>
        </div>
      </div>
      <div>
        <div id="flag-duty-options">
          <button
            onClick={() => onButtonClick("check")}
            style={{ marginBottom: "5px" }}
          >
            Flagged Menu
          </button>
          <button>Duty Status</button>
        </div>
        <div className="flex-center">
          <div className="duty-status-page">
            <div className="current-status-container">
              <h1 style={{ fontSize: "8px", color: "white" }}>
                CURRENT STATUS
              </h1>
              <div
                className="current-status"
                style={{ textAlign: "center", backgroundColor: status }}
              ></div>
            </div>
          </div>
        </div>
        <div id="duty-menu">
          <button id="on-duty" onClick={() => changeDuty(true)}>
            ON DUTY
          </button>
          <button id="off-duty" onClick={() => changeDuty(false)}>
            OFF DUTY
          </button>
        </div>
      </div>
    </div>
  );
};

export default Duty;
