import React, { useState } from "react";
import "./App.css";
import { fetchNui } from "../utils/fetchNui";
import { useNuiEvent } from "../hooks/useNuiEvent";

interface CheckProps {
  onButtonClick: (buttonType: string) => void;
}

const Remove: React.FC<CheckProps> = ({ onButtonClick }) => {
  const [plateNumber, setPlateNumber] = useState("");
  const [officersCount, setOfficersCount] = useState(0);
  useNuiEvent<any>("getOfficers", (data) => {
    setOfficersCount(data.count);
  });

  const removePlate = () => {
    fetchNui("removePlateNumber", { plate: plateNumber }); // I love react LOL
  };

  return (
    <div id="police-hub-container" className="police-hub-container">
      <div className="hub-container">
        <div className="police-hub">
          <h1>POLICE HUB</h1>
          <div className="purple-line"></div>
        </div>
        <div className="hub-buttons">
          <div onClick={() => onButtonClick("default")} className="hub-button">
            <h1 id="active-officers">{officersCount}</h1>
          </div>
          <div
            onClick={() => onButtonClick("mdtt")}
            id="tablet"
            className="hub-button"
          >
            <i className="fa-solid fa-tablet-button"></i>
          </div>
          <div
            onClick={() => onButtonClick("callsign")}
            id="pen"
            className="hub-button"
          >
            <i className="fa-solid fa-file-pen"></i>
          </div>
          <div
            onClick={() => onButtonClick("settings")}
            id="bars"
            className="hub-button toggle"
          >
            <i className="fa-solid fa-bars"></i>
          </div>
        </div>
      </div>
      <div>
        <div id="flag-duty-options">
          <button>Flagged Menu</button>
        </div>
        <div>
          <div id="flagged-menu">
            <button
              onClick={() => onButtonClick("check")}
              color="#d3d3d3;"
              id="check-btn"
            >
              CHECK
            </button>
            <button
              onClick={() => onButtonClick("newflag")}
              color="#d3d3d3;"
              id="new-flag-btn"
            >
              NEW FLAG
            </button>
            <button
              onClick={() => onButtonClick("removeflag")}
              className="clicked"
              color="#d3d3d3;"
              id="remove-btn"
            >
              REMOVE
            </button>
          </div>
        </div>
      </div>
      <div className="plate-input-container">
        <div className="plate-input">
          <div className="plate-line-upper"></div>
          <input
            type="text"
            onChange={(e) => setPlateNumber(e.target.value)}
            id="plate-input-text"
            placeholder="PLATE"
          />
          <div className="plate-line-bottom"></div>
        </div>
        <button id="check-plate-btn" onClick={removePlate}>
          REMOVE
        </button>
      </div>
    </div>
  );
};

export default Remove;
